import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'

import type { SelectChangeEvent } from '@mui/material'
import {
  CircularProgress,
  Container,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  MenuItem,
  Pagination,
  Select,
  Stack,
  Switch,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import CLUB_BELONGING from 'src/data/belonging'
import CLUB_CATEGORIES, { type ClubCategories } from 'src/data/categories'
import { CLUB_SEARCH_MESSAGE } from 'src/data/messages'
import {
  RECRUIT_SORTING_MAPPING,
  RECRUIT_SORTING_ORDERS,
  type RecruitSortingOrders
} from 'src/data/recruitNoticeSortingOrders'

import EmptySearchResult from 'src/components/EmptySearch'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import RecruitNoticePreview from 'src/components/RecruitNoticePreview'
import Searchbox from 'src/components/Searchbox'

import { useGetRecruitsByParam } from 'src/hooks/api/recruit'

const SELECT_COMPONENT_WIDTH = '150px'

const RecruitNoticePage = () => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()
  const token = authHeader()
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))

  const [searchParams, setSearchParams] = useSearchParams()
  const [isLoading, setIsLoading] = useState(false)

  const { data, isFetched } = useGetRecruitsByParam(searchParams)

  const recruits = data?.recruits ?? []
  const numPages = data?.numPages ?? 1
  const orderParam = searchParams.get('orderBy')
    ? searchParams.get('orderBy')?.toString() + '_' + searchParams.get('order')?.toString()
    : ''

  useEffect(() => {
    setIsLoading(!isFetched)
  }, [isFetched])

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    search.value ? searchParams.set('name', search.value) : searchParams.delete('name')
    setSearchParams(searchParams)
  }

  const handleSelectBoxChange = ({
    target: { name, value }
  }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    if (name === 'order') {
      const orderKey = value as RecruitSortingOrders | null
      const mappedOrder = orderKey ? { ...RECRUIT_SORTING_MAPPING[orderKey] } : null
      mappedOrder ? searchParams.set('orderBy', mappedOrder?.orderBy) : searchParams.delete('orderBy')
      mappedOrder ? searchParams.set('order', mappedOrder?.order) : searchParams.delete('order')
    } else value ? searchParams.set(name, value) : searchParams.delete(name)
    setSearchParams(searchParams)
  }

  const handleSwitchChange = ({ target: { name } }: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
    searchParams.delete('page')
    checked ? searchParams.set(name, 'true') : searchParams.delete(name)
    setSearchParams(searchParams)
  }

  const handlePageChange = (event: React.ChangeEvent<HTMLInputElement | unknown>, value: number) => {
    value === 1 ? searchParams.delete('page') : searchParams.set('page', `${value}`)
    setSearchParams(searchParams)
  }

  const handleClickCategory: React.MouseEventHandler<HTMLButtonElement> = event => {
    const category = event.currentTarget.getAttribute('data-value') as ClubCategories
    if (!CLUB_CATEGORIES.includes(category)) {
      enqueueSnackbar(CLUB_SEARCH_MESSAGE.WRONG_CATEGORY, { variant: 'warning' })
      return
    }
    if (category === searchParams.get('category')) {
      searchParams.delete('category')
      searchParams.delete('page')
      setSearchParams(searchParams)
      return
    }
    category ? searchParams.set('category', category) : searchParams.delete('category')
    setSearchParams(searchParams)
  }

  return (
    <>
      <Helmet>
        <title>모집 공고</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Stack direction="row" alignItems="baseline">
              <Typography variant={isMobile ? 'h4' : 'h3'} gutterBottom sx={{ marginTop: 1 }}>
                모집 공고
              </Typography>
            </Stack>
            <Typography variant={isMobile ? 'body2' : 'subtitle2'}>
              키워드와 카테고리, 분류를 활용해 원하는 동아리를 찾아보세요
            </Typography>
          </Grid>
        </Grid>
        <Searchbox
          handleSubmit={handleSubmit}
          searchDefaultValue={searchParams.get('name') ?? ''}
          filterCategory={(searchParams.get('category') as ClubCategories | '') ?? ''}
          handleClickCategory={handleClickCategory}
        />
      </PageTitleWrapper>
      <Container sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <Grid container width="100%" direction={isNotMobile ? 'row' : 'column'} spacing={1}>
          {token && (
            <FormControl size="small">
              <FormControlLabel
                control={
                  <Switch
                    name="isApplicableRecruit"
                    checked={searchParams.has('isApplicableRecruit') ? true : false}
                    onChange={handleSwitchChange}
                    color="primary"
                  />
                }
                label="내가 지원 가능한 공고만 보기"
                labelPlacement="start"
              />
            </FormControl>
          )}
          <Grid item sx={{ marginLeft: isNotMobile ? 'auto' : 0 }}>
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="belonging-label">소속</InputLabel>
              <Select
                labelId="belonging-label"
                id="belonging"
                name="belonging"
                label="소속"
                value={searchParams.get('belonging') ?? ''}
                onChange={handleSelectBoxChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {CLUB_BELONGING &&
                  CLUB_BELONGING.map((name, idx) => (
                    <MenuItem key={idx} value={name}>
                      {name}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item>
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="order-label">정렬</InputLabel>
              <Select
                labelId="order-label"
                id="order"
                name="order"
                label="정렬"
                value={orderParam}
                onChange={handleSelectBoxChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {RECRUIT_SORTING_ORDERS &&
                  Object.entries(RECRUIT_SORTING_ORDERS).map(([key, value]) => (
                    <MenuItem key={key} value={key}>
                      {value}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        {isLoading ? (
          <CircularProgress size={64} disableShrink thickness={3} />
        ) : recruits.length ? (
          <Grid container spacing={2} marginTop={1}>
            {recruits.map(recruit => (
              <Grid item xs={12} sm={12} md={6} key={recruit.id}>
                <RecruitNoticePreview {...recruit} />
              </Grid>
            ))}
          </Grid>
        ) : (
          <EmptySearchResult />
        )}

        {recruits.length !== 0 && (
          <Pagination
            page={parseInt(searchParams.get('page') ?? '1')}
            count={numPages ?? 1}
            size={isNotMobile ? 'medium' : 'small'}
            onChange={handlePageChange}
            sx={{ my: 3 }}
          />
        )}
      </Container>
    </>
  )
}

export default RecruitNoticePage
