import SearchIcon from '@mui/icons-material/Search'
import type { ButtonProps } from '@mui/material'
import {
  Box,
  Button,
  FormControl,
  IconButton,
  InputAdornment,
  OutlinedInput,
  styled,
  useMediaQuery,
  useTheme
} from '@mui/material'

import CLUB_CATEGORIES, { type ClubCategories } from 'src/data/categories'

interface SearchboxProps {
  handleSubmit: React.FormEventHandler<HTMLFormElement>
  searchDefaultValue: string
  handleClickCategory?: React.MouseEventHandler<HTMLButtonElement>
  filterCategory?: ClubCategories | ''
}

const Searchbox: React.FC<SearchboxProps> = ({
  handleSubmit,
  searchDefaultValue,
  handleClickCategory,
  filterCategory
}) => {
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
  return (
    <Box component="form" onSubmit={handleSubmit}>
      <FormControl variant="outlined" fullWidth sx={{ marginTop: isMobile ? 2 : 4, backgroundColor: 'white' }}>
        <OutlinedInput
          key={searchDefaultValue}
          id="search"
          type="search"
          placeholder="동아리 이름을 검색해보세요"
          defaultValue={searchDefaultValue}
          inputProps={{ maxLength: 50 }}
          sx={{ height: isMobile ? 40 : 48 }}
          endAdornment={
            <InputAdornment position="end">
              <IconButton aria-label="Search clubs" type="submit" edge="end">
                <SearchIcon />
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>
      <CategoryContainer>
        {CLUB_CATEGORIES &&
          CLUB_CATEGORIES.map((name, idx) => (
            <CategoryChip
              key={idx + name}
              variant={filterCategory === name ? 'contained' : 'outlined'}
              size="small"
              onClick={handleClickCategory}
              isMobile={isMobile}
              data-value={name}
              sx={{ py: filterCategory === name ? 0.6 : 0.5, px: filterCategory === name ? 1.6 : 1.5 }}
            >
              {name}
            </CategoryChip>
          ))}
      </CategoryContainer>
    </Box>
  )
}

export default Searchbox

interface CategoryChipProps extends ButtonProps {
  isMobile: boolean
}

const CategoryContainer = styled(Box)`
  display: flex;
  overflow-x: auto;
  padding: 8px 0;
  gap: 8px;
  &::-webkit-scrollbar {
    height: 4px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #ddd;
    border-radius: 4px;
  }
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }
`

const CategoryChip = styled(Button)<CategoryChipProps>`
  ${({ isMobile }) => `
    margin: 1px 1px 0 0;
    height: ${isMobile ? '28px' : '32px'};
    width: auto;
    white-space: nowrap; 
    display: flex;
    flex-direction: row;
    align-items: center;
    `}
`
