import { Link } from 'react-router-dom'

import FavoriteIcon from '@mui/icons-material/Favorite'
import { Box, Card, CardActionArea, Chip, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'

import { type ClubBelonging } from 'src/data/belonging'

import { stringToColor } from 'src/utils'
import getRecruitDDayLabel from 'src/utils/getRecruitDDayLabel'

export interface RecruitNoticePreviewProps {
  id: number
  clubId: number
  title: string
  expireIn?: number
  club: RecruitClub
  startAt: Date
}

interface RecruitClub {
  name: string
  category: string
  belonging: ClubBelonging
  likeCount: number
}

const today = new Date()

const RecruitNoticePreview = ({ id: recruitId, clubId, title, expireIn, club, startAt }: RecruitNoticePreviewProps) => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const dDay = expireIn ? expireIn / (24 * 60 * 60 * 1000) : undefined
  const startAtDate = new Date(startAt)
  const isRecruiting = startAtDate <= today

  return (
    <Card>
      <CardActionArea
        component={Link}
        to={`/club/${clubId}/recruit/${recruitId}`}
        sx={{ display: 'flex', alignItems: 'stretch', padding: isNotMobile ? 2 : 1.5, minHeight: '6.8em' }}
      >
        <Stack direction="column" flex="1" spacing={isNotMobile ? 1 : 0.6}>
          <Stack direction="row" spacing={1}>
            <Chip
              label={club.category}
              size="small"
              sx={{ padding: 0.5, fontSize: isNotMobile ? 13 : 11, paddingTop: 0.7 }}
            />
            {club.belonging && (
              <Chip
                label={club.belonging}
                size="small"
                sx={{ padding: 0.5, fontSize: isNotMobile ? 13 : 11, paddingTop: 0.7 }}
              />
            )}
          </Stack>
          <Box display="flex" alignItems="center">
            <Box
              width="100%"
              maxWidth="50px"
              height="50px"
              margin={1}
              sx={{ backgroundColor: stringToColor(club.name, 0.3), borderRadius: 70 }}
            />
            <Box>
              <Stack direction="row" spacing={1} alignItems="center" paddingLeft={0.5} paddingTop={0.5}>
                <Typography variant="h4">{club.name}</Typography>
              </Stack>
              <Typography
                paddingLeft={0.4}
                paddingTop={0.2}
                variant="body2"
                fontSize={15}
                flex={1}
                overflow="hidden"
                textOverflow="ellipsis"
                sx={{
                  wordBreak: 'keep-all',
                  display: '-webkit-box',
                  WebkitLineClamp: '2',
                  WebkitBoxOrient: 'vertical'
                }}
              >
                {title}
              </Typography>
            </Box>
          </Box>
          <Stack direction="row" alignItems="center" spacing={1}>
            <Chip
              label={isRecruiting ? '모집중' : '모집예정'}
              size="small"
              variant={isRecruiting ? 'filled' : 'outlined'}
              color="primary"
              sx={{
                padding: 0.5,
                fontSize: isNotMobile ? 13 : 11,
                paddingTop: 0.7
              }}
            />
            <Typography
              variant="body2"
              fontSize={15}
              color={dDay == 0 ? theme.colors.alpha.black[50] : theme.colors.primary.main}
              fontWeight="bold"
            >
              {getRecruitDDayLabel(dDay)}
            </Typography>
            <FavoriteIcon
              sx={{
                width: '0.6em',
                height: '0.6em',
                color: theme.colors.alpha.black[30]
              }}
            />
            <Typography variant="body2" fontSize={15} color={theme.colors.alpha.black[30]} marginLeft={0.2}>
              {club.likeCount}
            </Typography>
          </Stack>
        </Stack>
      </CardActionArea>
    </Card>
  )
}

/* 기존 컬러 배색 코드 (</Stack> 뒤) 
      {isNotMobile && (
          <Box display="flex" alignItems="center" width="130px">
            <Box
              width="100%"
              maxWidth="130px"
              height="130px"
              marginLeft={1}
              sx={{ backgroundColor: stringToColor(club.name, 0.3) }}
            />
          </Box>
        )}
*/

export default RecruitNoticePreview
